*{
    font-size: 20px;
    font-weight: 800;
}
.row{
width: min(90%,1400px);
background-color: ghostwhite;
box-shadow: 0 3px 5px #ccc;
padding: 20px;
border-radius: 10px;
margin: auto;
margin-bottom: 40px;
margin-top: 40px;
}
.TextArea{
 font-size: 20px;
 color:black;
 font-weight: 800;
 padding: 20px;

}
.Navbar{
    background-color: black;
 color:#00beed;
 padding: 20px;
 font-size: 20px;
 font-weight: 800;
box-shadow: 0 4px 8px black;
}
.logo{
    display: inline;
}
.Navbar li{
    display: inline;
    padding: 20px;
    color:#00beed;
    transition: 0.15s;
}
.Navbar li:hover{
    opacity: 0.8;
    font-size: 1.2em;
}
.Navbar ul{
    display: inline;
}
.Navbar button{
    border: none;
    font-size: 20px;
    background-color:#141414;
    padding: 10px;
    color:#00beed;
    box-shadow: 0 4px 8x black;
    border-radius: 10px;
}
.Navbar input[type="text"]{
    width: 5%;
    margin-left: 5%;
    margin-right: 5%;
    font-size: 20px;
    color:#00beed;
    background-color: #141414;
}
.Progress{
    height: 300px;
    width: 300px;
    border-radius: 300px;
    box-shadow: 0 3px 4px #ccc;
    display: inline-block;
    
}
body{
    background-color: #262729;
